<template>
    <div>
        <ul>
            <v-container>
                <v-card hover class="mx-auto"
                v-for="(event, index) in events" :key="index">
                    {{ event }}
                    <v-card-title class="justify-center">{{ event.name }}</v-card-title>
                    <v-card-subtitle>{{ event.details }}</v-card-subtitle>
                    <v-card-actions>
                        <v-spacer></v-spacer>
                        <v-btn>Edit</v-btn>
                        <v-btn>Delete</v-btn>
                        <v-btn>Share</v-btn>
                        <v-spacer></v-spacer>
                    </v-card-actions>
                </v-card>
            </v-container>
        </ul>
    </div>
</template>


<script>
import { db } from '@/main';
import { mapGetters } from "vuex";
import { mapState } from 'vuex';


export default {

    name: "list",
    data: () => ({
        events: []
    }),

    mounted() {
        this.getEvents();
    },

    computed: {

    ...mapState(['userid'])

  },
    methods: {
        async getEvents() {
            let snapshot = await db.collection('testingWebApp').get();
            let events = [];

            console.log("getEvents() called");

            snapshot.forEach(doc => {
                // console.log(doc.data());

                console.log("Doc user id : ", doc.data().userid);
                console.log("user id : ", this.$store.state.userid);

                if (this.userid == doc.data().userid) {
                  let appData = doc.data();
                  appData.id = doc.id;
                  events.push(appData);
                }
            });

            this.events = events;
        }
    }
}
</script>

